import React from "react";

export default function Todo({ todo, toggleTodo }) {
    function handleTodo() {
        toggleTodo(todo.id)
        console.log(`the button with this id ${todo.id} is clicked`)
    }
    return <div clas="todolist">
        <label>
            <input type='checkbox' onChange={handleTodo} checked={todo.complete} />
        {todo.name}
     </label>
  </div>;
}
